@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

html, body, #root {
    height: 100%;
}

input{font-family:Jost;font-size:24px;padding:0px;margin:5px;width:100%;text-align:center;background-color:transparent;border:solid 1px grey;border-radius:10px}input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield;-webkit-appearance:textfield;appearance:textfield}

.Settings{display:flex;min-width:100%;min-height:100%;align-items:center;flex-direction:column;font-family:Jost;font-size:32px}.color-settings,.type-settings{text-align:center;padding:50px;margin:50px;width:75%;-webkit-animation:fadeIn 1s;animation:fadeIn 1s}.setting-container{display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center}.setting{font-size:24px;text-align:center;padding:0px;margin:25px}.setting p{margin:5px;width:100%}@-webkit-keyframes fadeIn{0%{opacity:0}}@keyframes fadeIn{0%{opacity:0}}

.Display{font-family:Jost;font-size:36px;width:5vw;text-align:center}

.Timer{font-family:Jost;font-size:36px;width:5vw}

.Words{font-family:Jost;font-size:24px;text-align:left;width:50%}.Line{-webkit-animation:grow-height 0.2s;animation:grow-height 0.2s;max-height:45px;overflow:hidden}.notTyped{display:inline-block;overflow:hidden;transition:0.2s}.correct{display:inline-block;overflow:hidden;transition:0.2s}.incorrect{display:inline-block;overflow:hidden;max-width:20px;-webkit-animation:grow-width 0.2s;animation:grow-width 0.2s}@-webkit-keyframes grow-width{0%{max-width:0}}@keyframes grow-width{0%{max-width:0}}@-webkit-keyframes grow-height{0%{max-height:0}}@keyframes grow-height{0%{max-height:0}}

.Speed{font-family:Jost;font-size:36px;text-align:right;width:5vw}

.Type{display:flex;min-width:100%;min-height:100%;justify-content:center;outline:none}.Words{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);-webkit-animation:fadeIn 1s;animation:fadeIn 1s}.info{display:flex;width:95vw;justify-content:space-between;-webkit-animation:fadeIn 3s;animation:fadeIn 3s}@-webkit-keyframes fadeIn{0%{opacity:0}50%{opacity:0}}@keyframes fadeIn{0%{opacity:0}50%{opacity:0}}

.commands{position:fixed;text-align:left;caret-color:transparent;margin:10px;padding:10px;width:50vw;bottom:0;border:none;outline:none}

